.loader {
  width: 34px;
  height: 34px;
  background: url(./loader.svg) 0 0 no-repeat;
  -webkit-animation-name: loader-spin;
          animation-name: loader-spin;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: cubic-bezier(0.21, 0.64, 0.8, 0.41);
          animation-timing-function: cubic-bezier(0.21, 0.64, 0.8, 0.41);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  margin: auto;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  display: block;
}

@-webkit-keyframes loader-spin {
  from {
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }

  to {
    -webkit-transform: rotate(2220deg);
            transform: rotate(2220deg);
  }
}

@keyframes loader-spin {
  from {
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }

  to {
    -webkit-transform: rotate(2220deg);
            transform: rotate(2220deg);
  }
}



